body {
  margin: 0;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Arial, sans-serif;
  font-size: 100px;
  color: #fff;
}

@import "./variables";

@import "./container";
